/* base class */
/*清除浮动*/
.clearfix:before,
.clearfix:after {
  content: " ";
  display: inline-block;
  height: 0;
  clear: both;
  visibility: hidden;
}
/* flex */
.flex {
  display: flex;
}
.flex-around {
  justify-content: space-around;
}
.flex-between {
  justify-content: space-between;
}
/* 垂直居中 */
.flex-vertical-center {
  display: flex;
  align-items: center;
}
/* 水平居中 */
.flex-horizontal-center {
  display: flex;
  justify-content: center;
}
/* 垂直水平居中 */
.flex-both-center {
  display: flex;
  justify-content: center;
  align-items: center;
}
