.index .index-content {
  flex: 1;
  min-height: 100vh;
  background: #f0f2f5;
  /* tab-show 动画 */
  /* tab-change 动画 */
}
.index .index-content header {
  width: 100%;
  height: 64px;
  padding: 0;
  background: #fff;
  box-shadow: 0 1px 4px rgba(0, 21, 41, 0.08);
}
.index .index-content header .fold-menu-icon {
  width: 68px;
  height: 100%;
  box-sizing: border-box;
  cursor: pointer;
}
.index .index-content header .fold-menu-icon i {
  display: inline-block;
  width: 100%;
  height: 64px;
  background: url('~@assets/common/fold.svg') no-repeat center center;
  background-size: 20px 20px;
}
.index .index-content header .header-handle-items {
  padding: 0 5px;
  box-sizing: border-box;
}
.index .index-content header .header-handle-items > div {
  padding: 0 10px;
  box-sizing: border-box;
  cursor: pointer;
}
.index .index-content header .header-handle-items > div i {
  display: inline-block;
  width: 30px;
  height: 64px;
  vertical-align: middle;
  cursor: pointer;
}
.index .index-content header .header-handle-items > div:not(.handle-search):hover {
  background: rgba(0, 0, 0, 0.025);
}
.index .index-content header .header-handle-items .handle-search i {
  background: url('~@assets/common/search.svg') no-repeat center center;
  background-size: 16px 16px;
}
.index .index-content header .header-handle-items .handle-search .search-input {
  display: inline-block;
  width: 0px;
  transition: width 0.3s linear;
  overflow: hidden;
  border-bottom: 1px solid #d9d9d9;
  padding: 5px 0;
  box-sizing: border-box;
}
.index .index-content header .header-handle-items .handle-search .search-input input {
  width: 100%;
  border: none;
  border-radius: 0;
  outline: none;
}
.index .index-content header .header-handle-items .handle-search .search-input input:focus {
  box-shadow: none;
}
.index .index-content header .header-handle-items .handle-question {
  position: relative;
}
.index .index-content header .header-handle-items .handle-question a {
  display: inline-block;
  width: 30px;
  height: 64px;
  background: url('~@assets/common/question.svg') no-repeat center center;
  background-size: 16px 16px;
}
.index .index-content header .header-handle-items .handle-question .question-tip {
  position: absolute;
  bottom: -40px;
  left: -15px;
  width: 80px;
  text-align: center;
  height: 32px;
  line-height: 32px;
  color: #fff;
  font-size: 14px;
  background-color: rgba(0, 0, 0, 0.75);
  border-radius: 4px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
  display: none;
  transition: opacity 0.3s linear;
}
.index .index-content header .header-handle-items .handle-question .question-tip:after {
  content: "";
  display: inline-block;
  border-width: 0 5px 5px;
  border-color: transparent;
  border-bottom-color: rgba(0, 0, 0, 0.75);
  width: 0px;
  height: 0px;
  border-style: solid;
  position: absolute;
  top: 0;
  left: 50%;
  margin-left: -5px;
  top: -5px;
}
.index .index-content header .header-handle-items .handle-question:hover > .question-tip {
  display: block;
}
.index .index-content header .header-handle-items .handle-messages {
  position: relative;
}
.index .index-content header .header-handle-items .handle-messages i {
  background: url('~@assets/common/message.svg') no-repeat center center;
  background-size: 16px 16px;
}
.index .index-content header .header-handle-items .handle-messages span {
  display: inline-block;
  padding: 4px 6px;
  box-sizing: border-box;
  text-align: center;
  background: #f5222d;
  color: #fff;
  border-radius: 14px;
  position: absolute;
  top: 6px;
  left: 45%;
}
.index .index-content header .header-handle-items .handle-messages .self-tab .self-tab-pane .list {
  width: 100%;
  color: rgba(0, 0, 0, 0.65);
  border-bottom: 1px solid #e8e8e8;
  padding: 12px 24px;
  box-sizing: border-box;
  transition: all 0.3s ease;
}
.index .index-content header .header-handle-items .handle-messages .self-tab .self-tab-pane .list:hover {
  background: #e6f7ff;
}
.index .index-content header .header-handle-items .handle-messages .self-tab .self-tab-pane .list:nth-last-child(2) {
  border-bottom: none;
}
.index .index-content header .header-handle-items .handle-messages .self-tab .self-tab-pane .notification-list .notification-list-left,
.index .index-content header .header-handle-items .handle-messages .self-tab .self-tab-pane .msg-list .notification-list-left,
.index .index-content header .header-handle-items .handle-messages .self-tab .self-tab-pane .notification-list .msg-list-left,
.index .index-content header .header-handle-items .handle-messages .self-tab .self-tab-pane .msg-list .msg-list-left {
  width: 32px;
  height: 32px;
  margin-right: 20px;
}
.index .index-content header .header-handle-items .handle-messages .self-tab .self-tab-pane .notification-list .notification-list-left img,
.index .index-content header .header-handle-items .handle-messages .self-tab .self-tab-pane .msg-list .notification-list-left img,
.index .index-content header .header-handle-items .handle-messages .self-tab .self-tab-pane .notification-list .msg-list-left img,
.index .index-content header .header-handle-items .handle-messages .self-tab .self-tab-pane .msg-list .msg-list-left img {
  width: 100%;
  height: 100%;
}
.index .index-content header .header-handle-items .handle-messages .self-tab .self-tab-pane .notification-list .notification-list-right,
.index .index-content header .header-handle-items .handle-messages .self-tab .self-tab-pane .msg-list .notification-list-right,
.index .index-content header .header-handle-items .handle-messages .self-tab .self-tab-pane .notification-list .msg-list-right,
.index .index-content header .header-handle-items .handle-messages .self-tab .self-tab-pane .msg-list .msg-list-right {
  text-align: left;
  flex: 1;
}
.index .index-content header .header-handle-items .handle-messages .self-tab .self-tab-pane .notification-list .notification-list-right > div,
.index .index-content header .header-handle-items .handle-messages .self-tab .self-tab-pane .msg-list .notification-list-right > div,
.index .index-content header .header-handle-items .handle-messages .self-tab .self-tab-pane .notification-list .msg-list-right > div,
.index .index-content header .header-handle-items .handle-messages .self-tab .self-tab-pane .msg-list .msg-list-right > div {
  line-height: 1.5;
}
.index .index-content header .header-handle-items .handle-messages .self-tab .self-tab-pane .notification-list .notification-list-right .top,
.index .index-content header .header-handle-items .handle-messages .self-tab .self-tab-pane .msg-list .notification-list-right .top,
.index .index-content header .header-handle-items .handle-messages .self-tab .self-tab-pane .notification-list .msg-list-right .top,
.index .index-content header .header-handle-items .handle-messages .self-tab .self-tab-pane .msg-list .msg-list-right .top {
  margin-bottom: 0.5em;
}
.index .index-content header .header-handle-items .handle-messages .self-tab .self-tab-pane .notification-list .notification-list-right .bottom,
.index .index-content header .header-handle-items .handle-messages .self-tab .self-tab-pane .msg-list .notification-list-right .bottom,
.index .index-content header .header-handle-items .handle-messages .self-tab .self-tab-pane .notification-list .msg-list-right .bottom,
.index .index-content header .header-handle-items .handle-messages .self-tab .self-tab-pane .msg-list .msg-list-right .bottom {
  font-size: 12px;
  color: rgba(0, 0, 0, 0.45);
}
.index .index-content header .header-handle-items .handle-messages .self-tab .self-tab-pane .event-list .event-list-top {
  line-height: 22px;
  margin-bottom: 8px;
}
.index .index-content header .header-handle-items .handle-messages .self-tab .self-tab-pane .event-list .event-list-top .event-status {
  font-size: 12px;
  border-style: solid;
  border-width: 1px;
  border-radius: 4px;
  padding: 0 7px;
  box-sizing: border-box;
}
.index .index-content header .header-handle-items .handle-messages .self-tab .self-tab-pane .event-list .event-list-bottom {
  text-align: left;
  line-height: 1.5;
  font-size: 12px;
  color: rgba(0, 0, 0, 0.45);
}
.index .index-content header .header-handle-items .handle-messages .self-tab .self-tab-pane .tab-footer {
  height: 44px;
  line-height: 44px;
  border-top: 1px solid #e8e8e8;
}
.index .index-content header .header-handle-items .handle-messages .self-tab .self-tab-pane .tab-footer .tab-footer-item {
  width: 50%;
  text-align: center;
}
.index .index-content header .header-handle-items .handle-messages .self-tab .self-tab-pane .tab-footer .tab-footer-item:hover {
  background: rgba(0, 0, 0, 0.025);
}
.index .index-content header .header-handle-items .handle-messages .self-tab .self-tab-pane .tab-footer .tab-footer-item:last-child {
  border-left: 1px solid #e8e8e8;
}
.index .index-content header .header-handle-items .handle-person-setting {
  position: relative;
}
.index .index-content header .header-handle-items .handle-person-setting .person-info {
  height: 64px;
}
.index .index-content header .header-handle-items .handle-person-setting .person-info img {
  width: 24px;
  height: 24px;
  border-radius: 12px;
}
.index .index-content header .header-handle-items .handle-person-setting .person-info span {
  margin: 0 10px;
}
.index .index-content header .header-handle-items .handle-person-setting .person-handle-content {
  width: 160px;
  position: absolute;
  right: 0;
  top: 60px;
  padding-top: 10px;
}
.index .index-content header .header-handle-items .handle-person-setting .person-handle-content ul {
  background-color: #fff;
  border-radius: 4px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
  padding: 4px 0;
}
.index .index-content header .header-handle-items .handle-person-setting .person-handle-content ul li.person-handle-item {
  color: rgba(0, 0, 0, 0.65);
  font-weight: 400;
  font-size: 14px;
  line-height: 22px;
  white-space: nowrap;
  cursor: pointer;
  transition: all 0.3s;
  padding: 5px 12px;
}
.index .index-content header .header-handle-items .handle-person-setting .person-handle-content ul li.person-handle-item svg {
  vertical-align: -0.1rem;
  margin-right: 8px;
}
.index .index-content header .header-handle-items .handle-person-setting .person-handle-content ul li.person-handle-divider {
  height: 1px;
  margin: 4px 0;
  overflow: hidden;
  line-height: 0;
  background-color: #e8e8e8;
}
.index .index-content header .header-handle-items .handle-person-setting .person-handle-content ul li.person-handle-item:hover {
  background: #e6f7ff;
}
.index .index-content .scaleY-enter {
  opacity: 0.01;
  transform: scaleY(0.8);
  transform-origin: 0 0;
}
.index .index-content .scaleY-enter-active {
  opacity: 1;
  transform: scaleY(1);
  transform-origin: 0 0;
  transition: all 200ms ease-out;
}
.index .index-content .scaleY-exit {
  opacity: 1;
  transform: scaleY(1);
  transform-origin: 0 0;
}
.index .index-content .scaleY-exit-active {
  opacity: 0;
  transform: scaleY(0.8);
  transform-origin: 0 0;
  transition: all 200ms ease-in;
}
.index .index-content .opacity-enter {
  opacity: 0.01;
}
.index .index-content .opacity-enter-active {
  opacity: 1;
  transition: all 0.45s ease-out;
}
.index .index-content .opacity-exit {
  opacity: 1;
}
.index .index-content .opacity-exit-active {
  opacity: 0;
  transition: all 0.45s ease-in;
}
